export default {
	data() {
		return {
			userinfo: '',
		}
	},
	onLoad() {
		console.log('logout页面onLoad');
		this.userinfo = uni.getStorageSync('userinfo');
	},
	methods: {
		logout() {
			uni.showLoading({
				title: 'waiting',
				mask: false
			});
			
			uni.removeStorageSync('userinfo');
					
			uni.hideLoading();
			uni.reLaunch({
				url: 'index'
			});
			
			
			
		}
	}
}
